import * as React from "react"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import Seo from "../components/seo"

const PoolCarePage = ({ data, location }) => {
  const services = data.allMarkdownRemark.nodes
  const getImageFromGatsby = image => getImage(image)
  return (
    <Layout>
      <Seo title="PoolCare" />
      {/* <h1>Hi people</h1>
    <p>
      <Link to="/page-2/">Go to page 2</Link> <br />
      <Link to="/using-typescript/">Go to "Using TypeScript"</Link>
    </p> */}
      {services &&
        services.map(service => (
          <section className="page-section" key={service.id}>
            <div className="container">
              <div className="product-item">
                <div className="product-item-title d-flex">
                  <div className="bg-faded p-5 pb-3 d-flex me-auto rounded flex-column">
                    <h2 className="section-heading mb-2">
                      <span className="section-heading-upper">
                        {service.frontmatter.subtitle}
                      </span>
                      <span className="section-heading-lower">
                        {service.frontmatter.title}
                      </span>
                    </h2>
                    <div className="intro-button mx-auto">
                      <a className="btn btn-primary btn-xl" href={"/pool-care/articles/" + service.frontmatter.url} >
                        See more details
                      </a>
                    </div>
                  </div>
                </div>
                <div className="left-0 text-center rounded">
                  <GatsbyImage
                    className="product-item-img mx-auto d-flex rounded img-fluid mb-3 mb-lg-0 custom-box-shadow"
                    image={getImageFromGatsby(
                      service.frontmatter.thumbnailImage
                    )}
                    quality={95}
                    width={500}
                    placeholder="blurred"
                    formats={["auto", "webp", "avif"]}
                    alt="sun"
                  />
                </div>
              </div>
            </div>
          </section>
        ))}
    </Layout>
  )
}

export default PoolCarePage

export const pageQuery = graphql`
query ListOfPoolCareAtricles {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(filter: { frontmatter: { type: { eq: "article" } } }) {
      nodes {
        id
        frontmatter {
          title
          subtitle
          url
          thumbnailImage {
            childImageSharp {
              gatsbyImageData
            }
          }
        }
      }
    }
  }
`
